<template>
  <!-- 机构管理 -->
  <div class="orgInformationManage">
    <el-container style="height: 100%;">
      <el-aside width="20%">
        <el-input
          v-model="filterText"
          placeholder="机构简称"
          size="small"
          style="padding:0px 10px 10px;"
        ></el-input>
        <el-scrollbar :style="Height">
          <div class="organization-tree">
            <el-tree
              :data="orgDataTree"
              node-key="id"
              ref="tree3"
              highlight-current
              :expand-on-click-node="clickNode"
              :filter-node-method="filterNode"
              @node-click="handleNodeOrgTreeClick"
            >
              <div class="custom-tree-node" slot-scope="{ node}">
                <div>
                  <span class="danwei"></span>
                  {{ node.label }}
                </div>
              </div>
            </el-tree>
          </div>
        </el-scrollbar>
      </el-aside>
      <el-main>
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
          <el-tab-pane label="机构信息" name="first" :key="'first'">
            <org-manage
              v-if="isUpdateManage"
              ref="changeManage"
              :orgCode="orgId"
              :labelName="labelNames"
              @selectOrgTree="selectOrgTree"
            ></org-manage>
          </el-tab-pane>
          <el-tab-pane label="编制信息" name="second" :key="'second'">
            <org-staff
              v-if="isUpdateStaff"
              ref="changeStaff"
              :orgCode="orgId"
              :labelName="labelNames"
            ></org-staff>
          </el-tab-pane>
          <el-tab-pane label="关联附件" name="three" :key="'three'">
            <org-file-upload
              v-if="isUpdateFile"
              ref="changeFile"
              :orgCode="orgId"
              :labelName="labelNames"
            ></org-file-upload>
          </el-tab-pane>
        </el-tabs>
      </el-main>
    </el-container>
  </div>
</template>
<script>
import orgManage from "./orgManage.vue";
import orgStaff from "./orgStaff.vue";
import orgFileUpload from "./orgFileUpload.vue";

export default {
  name: "orgInformationManage",
  //import引入的组件需要注入到对象中才能使用
  components: {
    orgManage,
    orgStaff,
    orgFileUpload
  },
  props: [""],
  data() {
    return {
      Height: {
        height: document.documentElement.clientHeight - 235 + "px"
      },
      clickNode: false,
      filterText: "",
      activeName: "first",
      isUpdateManage: true,
      isUpdateStaff: false,
      isUpdateFile: false,
      orgId: "",
      labelNames: "",
      orgDataTree: [],
      tabname: "first"
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
    filterText(val) {
      this.$refs.tree3.filter(val);
    }
  },
  //方法集合
  methods: {
    filterNode(value, data, node) {
      if (!value) {
        return true;
      }
      let level = node.level;
      let _array = []; //这里使用数组存储 只是为了存储值。
      this.getReturnNode(node, _array, value);
      let result = false;
      _array.forEach(item => {
        result = result || item;
      });
      return result;
    },
    getReturnNode(node, _array, value) {
      let isPass =
        node.data && node.data.label && node.data.label.indexOf(value) !== -1;
      isPass ? _array.push(isPass) : "";
      if (!isPass && node.level != 1 && node.parent) {
        this.getReturnNode(node.parent, _array, value);
      }
    },
    //查询机构树
    selectOrgTree() {
      let _this = this;
      _this.$axios.post(_this.$api.org.selectOrgTree).then(response => {
        if (response.data.code === 0) {
          let res = response.data;
          _this.orgDataTree = res.data;
        } else {
          _this.$message.error(response.data.msg);
        }
      });
    },
    handleNodeOrgTreeClick(data) {
      let _this = this;
      _this.orgId = data.id;
      _this.labelNames = data.label;
      if (_this.tabname === "first") {
        setTimeout(() => {
          _this.$refs.changeManage.selectOrgById();
          _this.$refs.changeManage.labelNames = _this.labelNames;
          _this.$refs.changeManage.edit = true;
        }, 10);
      } else if (_this.tabname === "second") {
        setTimeout(() => {
          _this.$refs.changeStaff.selectStaff();
          _this.$refs.changeStaff.labelNames = _this.labelNames;
          _this.$refs.changeStaff.editAble = true;
        }, 10);
      } else {
        setTimeout(() => {
          _this.$refs.changeFile.selectAttachment();
          _this.$refs.changeFile.labelNames = _this.labelNames;
        }, 10);
      }
    },
    handleClick(tab) {
      let _this = this;
      _this.tabname = tab.name;
      if (tab.name === "first") {
        _this.isUpdateManage = true;
        _this.isUpdateStaff = false;
        _this.isUpdateFile = false;
        if (_this.orgId) {
          setTimeout(() => {
            _this.$refs.changeManage.selectOrgById();
            _this.$refs.changeManage.labelNames = _this.labelNames;
          }, 10);
        }
      } else if (tab.name == "second") {
        _this.isUpdateManage = false;
        _this.isUpdateStaff = true;
        _this.isUpdateFile = false;
        setTimeout(() => {
          _this.$refs.changeStaff.selectStaff();
          _this.$refs.changeStaff.labelNames = _this.labelNames;
        }, 10);
      } else {
        _this.isUpdateManage = false;
        _this.isUpdateStaff = false;
        _this.isUpdateFile = true;
        setTimeout(() => {
          _this.$refs.changeFile.selectAttachment();
          _this.$refs.changeFile.labelNames = _this.labelNames;
        }, 10);
      }
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.selectOrgTree();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},

  beforeCreate() {}, //生命周期 - 创建之前

  beforeMount() {}, //生命周期 - 挂载之前

  beforeUpdate() {}, //生命周期 - 更新之前

  updated() {}, //生命周期 - 更新之后

  beforeDestroy() {}, //生命周期 - 销毁之前

  destroyed() {}, //生命周期 - 销毁完成

  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>

<style lang="scss" scoped>

.el-aside {
  border-right: 1px solid #e6e6e6;
  padding-top: 10px;
}
.el-main{ padding: 0px!important;}
.organization-tree {
  height: 100%;
  .el-tree {
    width: 100%;
    font-size: 14px;
    overflow-y: auto;
    display: inline-block;
    .el-tree-node:focus > .el-tree-node__content {
      background-color: #88b1ca !important;
      color: #fff;
    }
  }
//   .xunijigou,
//   .danwei,
//   .bumen {
//     display: inline-block;
//     width: 16px;
//     height: 16px;
//   }
//   .xunijigou {
//     background: url("../../../../src/assets/images/xunijigou.gif") no-repeat 0
//       3px;
//     padding-left: 5px;
//   }
//   .danwei {
//     background: url("../../../../src/assets/images/danwei.png") no-repeat 0 3px;
//     padding-left: 5px;
//   }
//   .bumen {
//     background: url("../../../../src/assets/images/bumen.png") no-repeat 0 3px;
//     padding-left: 5px;
//   }
}
</style>


