<template>
    <div class="orgStaff">
        <div class="fileTitle">
      <span class="explain">
        <i class="el-icon-more"></i>
        {{labelNames}}
      </span>
            <div class="btnStaff" v-show="isEditShow">
                <el-button size="mini" type="primary"  @click="editStaff">编辑</el-button>
                <el-button size="mini" type="danger"  @click="deleteStaff">删除</el-button>
            </div>
            <div class="btnStaff" v-show="isSaveShow">
                <el-button size="mini" type="primary"  @click="saveStaff">保存</el-button>
                <el-button size="mini" type="danger" @click="cancelStaff">取消</el-button>
            </div>
        </div>
        <el-form
                :label-position="labelPosition"
                label-width="260px"
                :model="formStaff"
                :disabled="editAble"
                ref="formStaff"
                 size="small"
        >
            <!--<el-col :span="24">-->
            <!--<el-form-item label="单位职数描述">-->
            <!--<el-input type="text" v-model="formStaff.b0201"></el-input>-->
            <!--</el-form-item>-->
            <!--</el-col>-->
            <el-col :span="24">
                <el-col :span="12">
                    <el-form-item label="编制批准日期">
                        <el-input type="text" v-model="formStaff.b0201"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="编制批准文号">
                        <el-input type="text" v-model="formStaff.b0204"></el-input>
                    </el-form-item>
                </el-col>
            </el-col>
            <el-col :span="24">
                <el-col :span="12">
                    <el-form-item label="编制批准机关名称">
                        <el-input type="text" v-model="formStaff.b0207a"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="编制批准机关代码">
                        <el-input type="text" v-model="formStaff.b0207b"></el-input>
                    </el-form-item>
                </el-col>
            </el-col>
            <el-col :span="24">
                <el-col :span="12">
                    <el-form-item label="内设机构数">
                        <el-input type="text" v-model="formStaff.b0211"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="内设机构名称">
                        <el-input type="text" v-model="formStaff.b0214"></el-input>
                    </el-form-item>
                </el-col>
            </el-col>
            <el-col :span="24">
                <el-col :span="12">
                    <el-form-item label="内设机构级别">
                        <el-input type="text" v-model="formStaff.b0217"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="编制性质">
                        <el-input type="text" v-model="formStaff.b0221"></el-input>
                    </el-form-item>
                </el-col>
            </el-col>
            <el-col :span="24">
                <el-col :span="12">
                    <el-form-item label="批准编制总数">
                        <el-input type="text" v-model="formStaff.b0224"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="行政编制数">
                        <el-input type="text" v-model="formStaff.b0227"></el-input>
                    </el-form-item>
                </el-col>
            </el-col>
            <el-col :span="24">
                <el-col :span="12">
                    <el-form-item label="事业编制数">
                        <el-input type="text" v-model="formStaff.b0231"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="企业编制数">
                        <el-input type="text" v-model="formStaff.b0234"></el-input>
                    </el-form-item>
                </el-col>
            </el-col>
            <el-col :span="24">
                <el-col :span="12">
                    <el-form-item label="社团编制数">
                        <el-input type="text" v-model="formStaff.b0237"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="单位领导职数">
                        <el-input type="text" v-model="formStaff.b0241"></el-input>
                    </el-form-item>
                </el-col>
            </el-col>
            <el-col :span="24">
                <el-col :span="12">
                    <el-form-item label="单位领导同级非领导职务职数">
                        <el-input type="text" v-model="formStaff.b0244"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="内设第一级机构领导职数">
                        <el-input type="text" v-model="formStaff.b0247"></el-input>
                    </el-form-item>
                </el-col>
            </el-col>
            <el-col :span="24">
                <el-col :span="12">
                    <el-form-item label="内设第一级机构领导同级非领导职务">
                        <el-input type="text" v-model="formStaff.b0251"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="业务人员编制数">
                        <el-input type="text" v-model="formStaff.b0254"></el-input>
                    </el-form-item>
                </el-col>
            </el-col>
            <el-col :span="24">
                <el-col :span="12">
                    <el-form-item label="工勤人员编制数">
                        <el-input type="text" v-model="formStaff.b0257"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="使用全额拨款人数">
                        <el-input type="text" v-model="formStaff.b0261"></el-input>
                    </el-form-item>
                </el-col>
            </el-col>
            <el-col :span="24">
                <el-col :span="12">
                    <el-form-item label="使用差额拨款人数">
                        <el-input type="text" v-model="formStaff.b0264"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="使用自筹资金人数">
                        <el-input type="text" v-model="formStaff.b0267"></el-input>
                    </el-form-item>
                </el-col>
            </el-col>
        </el-form>
    </div>
</template>
<script>
    export default {
        name: "orgStaff",
        //import引入的组件需要注入到对象中才能使用
        props: {
            orgCode: {
                type: String
            },
            labelNames: {
                type: String
            }
        },
        components: {},

        data() {
            return {
                labelPosition: "left",
                isEditShow: true,
                isSaveShow: false,
                formStaff: {
                    b0111: "",
                    b0201: "",
                    b0204: "",
                    b0207a: "",
                    b0207b: "",
                    b0211: "",
                    b0214: "",
                    b0217: "",
                    b0221: "",
                    b0224: "",
                    b0227: "",
                    b0231: "",
                    b0234: "",
                    b0237: "",
                    b0241: "",
                    b0244: "",
                    b0247: "",
                    b0251: "",
                    b0254: "",
                    b0257: "",
                    b0261: "",
                    b0264: "",
                    b0267: ""
                },
                editAble: true
            };
        },
        //监听属性 类似于data概念
        computed: {},

        //监控data中的数据变化
        watch: {
            editAble(val)
            {
                if(val)
                {
                    this.isEditShow = true;
                    this.isSaveShow = false;
                }
                else {
                    this.isEditShow = false;
                    this.isSaveShow = true;
                }
            }
        },

        //方法集合
        methods: {
            //查询编制信息
            selectStaff() {
                let _this = this;
                if (_this.orgCode != "") {
                    let postData = {b0111: _this.orgCode};
                    _this.$axios
                        .post(_this.$api.org.selectB02Byb0111, postData)
                        .then(response => {
                            if (response.data.code === 0) {
                                let res = response.data;
                                if (res.data) {
                                    _this.formStaff = res.data;
                                } else {
                                    // _this.$refs["formStaff"].resetFields();
                                    _this.clearForm();
                                }
                            } else {
                                _this.$message.error(response.data.msg);
                            }
                        });
                }
            },
            //编辑
            editStaff() {
                let _this = this;
                _this.editAble = false;
            },
            //删除
            deleteStaff() {
                let _this = this;
                _this.editAble = true;
                _this
                    .$confirm("是否确认此操作?", "提示", {
                        confirmButtonText: "确定",
                        cancelButtonText: "取消",
                        type: "warning"
                    })
                    .then(() => {
                        if (_this.orgCode != "") {
                            let postData = {b0111: _this.orgCode};
                            _this.$axios
                                .post(_this.$api.org.deleteB02, postData)
                                .then(response => {
                                    if (response.data.code === 0) {
                                        _this.$message.success("删除成功");
                                        _this.selectStaff();
                                    } else {
                                        _this.$message.error(response.data.msg);
                                    }
                                });
                        }
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: "已取消删除"
                        });
                    });
            },
            clearForm(){
                this.formStaff = {
                    b0111: "",
                    b0201: "",
                    b0204: "",
                    b0207a: "",
                    b0207b: "",
                    b0211: "",
                    b0214: "",
                    b0217: "",
                    b0221: "",
                    b0224: "",
                    b0227: "",
                    b0231: "",
                    b0234: "",
                    b0237: "",
                    b0241: "",
                    b0244: "",
                    b0247: "",
                    b0251: "",
                    b0254: "",
                    b0257: "",
                    b0261: "",
                    b0264: "",
                    b0267: ""
                }
            },
            //保存
            saveStaff() {
                let _this = this;
                _this.editAble = true;
                if (_this.orgCode != "") {
                    let postData = _this.formStaff;
                    let url = _this.$api.org.updateB02;
                    if (postData.b0111 == '') {
                        _this.formStaff.b0111 = _this.orgCode;
                        url = _this.$api.org.insertB02;
                    }
                    _this.$axios.post(url, postData).then(response => {
                        if (response.data.code === 0) {
                            _this.$message.success("保存成功");
                            _this.cancelStaff();
                        } else {
                            _this.$message.error(response.data.msg);
                        }
                    });
                }
            },
            //取消
            cancelStaff() {
                let _this = this;
                _this.isEditShow = true;
                _this.isSaveShow = false;
                _this.editAble = true;
            }
        },

        //生命周期 - 创建完成（可以访问当前this实例）
        created() {
        },

        //生命周期 - 挂载完成（可以访问DOM元素）
        mounted() {
        },

        beforeCreate() {
        }, //生命周期 - 创建之前

        beforeMount() {
        }, //生命周期 - 挂载之前

        beforeUpdate() {
        }, //生命周期 - 更新之前

        updated() {
        }, //生命周期 - 更新之后

        beforeDestroy() {
        }, //生命周期 - 销毁之前

        destroyed() {
        }, //生命周期 - 销毁完成

        activated() {
        } //如果页面有keep-alive缓存功能，这个函数会触发
    };
</script>

<style scoped>
    .fileTitle {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 8px;
    }

    .explain {
        display: inline-block;
        color: #ff9966;
    }
    .btnStaff{
        margin-right: 10px;
    }
    .btnStaff > el-botton :nth-child(2) {
        margin-left: 10px;
       
    }

    .orgStaff >>> .el-form-item__label {
        border: 1px solid #dcdfe6;
        border-right: 0;
        padding-left: 10px;
    }

    .orgStaff >>> .el-input {
        border: 1px solid #dcdfe6;
    }

    .orgStaff >>> .el-form-item__content {
        height: 34px;
        line-height: 34px;
    }

    .orgStaff >>> .el-input__inner {
        border-radius: 0;
        border: 0;
        height: 34px;
        line-height: 34px;
    }

    .orgStaff >>> .el-form-item {
        margin-bottom: 12px;
    }

    .orgStaff >>> .el-form-item__label {
        line-height: 34px;
    }
</style>


