<template>
    <div class="orgFile">
        <div class="fileTitle">
      <span class="explain">
        <i class="el-icon-more"></i>
        {{labelNames}}
      </span>
            <div class="upload-container">
                <el-upload
                        class="upload"
                        ref="upload"
                        multiple
                        action
                        :auto-upload="false"
                        :http-request="uploadFile"
                >
                    <el-button slot="trigger" size="mini" type="primary" >选取文件</el-button>
                    <el-button
                            style="margin-left: 10px;"
                            size="mini"
                            type="primary"
                            
                            @click="submitUpload"
                    >上传到服务器
                    </el-button>
                </el-upload>
            </div>
        </div>
        <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
                >
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column
                    label="序号"
                    width="55">
            </el-table-column>
            <el-table-column
                    prop="fileName"
                    label="文件名称"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="remark"
                    label="摘要"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    prop="fileSize"
                    label="文件大小"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    prop="uploadTime"
                    label="上传时间"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column label="操作" width="130">
                <template slot-scope="scope">
                    <el-button
                            icon="el-icon-view"
                            size="mini"
                            type="primary"
                            title="预览"
                            circle
                            @click="handleEdit(scope.$index, scope.row)">
                    </el-button>
                    <el-button
                            size="mini"
                            icon="el-icon-close-solid"
                            title="删除"
                            circle
                            @click="handleDelete(scope.$index, scope.row)">
                    </el-button>
                    <el-button
                            size="mini"
                            icon="el-icon-download"
                            type="primary"
                            title="下载"
                            circle
                            @click="downLoadFile(scope.$index, scope.row)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <org-file-preview ref="changePreview"></org-file-preview>
    </div>
</template>
<script>
    import orgFilePreview from "./orgFilepreview.vue";

    export default {
        name: "orgFileUpload",
        //import引入的组件需要注入到对象中才能使用
        props: {
            orgCode: {
                type: String
            },
            labelNames: {
                type: String
            }
        },
        components: {
            orgFilePreview,
        },

        data() {
            return {
                tableData: [],
                fileData: new FormData(),
                multipleSelection: []
            };
        },
        //监听属性 类似于data概念
        computed: {},

        //监控data中的数据变化
        watch: {},

        //方法集合
        methods: {
            //查询附件
            selectAttachment() {
                let _this = this;
                if (_this.orgCode != "") {
                    let postData = {b0111: _this.orgCode};
                    _this.$axios
                        .post(_this.$api.org.selectAttachmentByB0111, postData)
                        .then(response => {
                            if (response.data.code === 0) {
                                let res = response.data;
                                _this.tableData = res.data;
                            } else {
                                _this.$message.error(response.data.msg);
                            }
                        });
                }
            },
            //附件删除
            handleDelete(index, row) {
                let _this = this;
                _this
                    .$confirm("是否确认此操作?", "提示", {
                        confirmButtonText: "确定",
                        cancelButtonText: "取消",
                        type: "warning"
                    })
                    .then(() => {
                        _this.$axios
                            .post(_this.$api.org.deleteAttachmentById, row)
                            .then(response => {
                                if (response.data.code === 0) {
                                    _this.$message.success(response.data.msg);
                                    _this.selectAttachment();
                                } else {
                                    _this.$message.error(response.data.msg);
                                }
                            });
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: "已取消删除"
                        });
                    });
            },
            //上传附件
            //覆盖默认的上传行为，可以自定义上传的实现
            uploadFile(file) {
                let _this = this;
                _this.fileData.append("files", file.file); //服务器最终接收到的是files[]数组
            },
            submitUpload() {
                let _this = this;
                _this.$refs.upload.submit(); //手动上传文件，此处表单提交后会自动调用uploadFile()方法，如果是多个文件就调用多次
                _this.fileData.append("orgCode", _this.orgCode);
                let config = {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    },
                    transformRequest: [function (data) {
                        return data
                    }]
                };
                //传给server的文件数据，server可自行解析文件相关属性
                _this.$axios
                    .post(_this.$api.org.uplodeB01Attachment, this.fileData, config
                    )
                    .then(response => {
                        let resp = response.data;
                        if (resp.code === 0) {
                            _this.$message({
                                message: "上传成功",
                                type: "success"
                            });
                            _this.$refs.upload.clearFiles();
                            _this.fileData = new FormData();
                            _this.selectAttachment();
                        } else {
                            _this.fileData.delete("orgCode");
                            _this.$message({
                                message: "上传失败，请重试",
                                type: "error"
                            });
                        }
                    });
            },
            //下载附件
            downLoadFile(index, row) {
                let _this = this;
                _this.$axios({
                    url: _this.$api.org.downloadB01Attachment,
                    data: row,
                    method: "post",
                    responseType: 'blob'
                })
                    .then(response => {
                        let blob = new Blob([response.data]);
                        if (window.navigator.msSaveOrOpenBlob) {
                            navigator.msSaveBlob(blob, row.fileName);
                        } else {
                            let link = document.createElement('a');
                            link.href = window.URL.createObjectURL(blob);
                            link.download = row.fileName;
                            link.click();
                            //释放内存
                            window.URL.revokeObjectURL(link.href);
                        }
                    });
            },
            //预览
            handleEdit(index, row) {
                this.$refs.changePreview.previewPdf(row)
            },
            handleSelectionChange(val) {
                this.multipleSelection = val
            }
        },

        //生命周期 - 创建完成（可以访问当前this实例）
        created() {
            // this.selectAttachment()
        },

        //生命周期 - 挂载完成（可以访问DOM元素）
        mounted() {
           
        },

        beforeCreate() {
        }, //生命周期 - 创建之前

        beforeMount() {
        }, //生命周期 - 挂载之前

        beforeUpdate() {
        }, //生命周期 - 更新之前

        updated() {
        }, //生命周期 - 更新之后

        beforeDestroy() {
        }, //生命周期 - 销毁之前

        destroyed() {
        }, //生命周期 - 销毁完成

        activated() {
        } //如果页面有keep-alive缓存功能，这个函数会触发
    };
</script>

<style scoped>
    .fileTitle {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .upload-container{
        margin :5px  10px 10px 0 ;
    }
    .explain {
        display: inline-block;
        color: #ff9966;
    }
</style>


