<template>
    <!-- 机构管理 -->
    <div class="orgManage">
        <div class="orgButton">
            <div class="left">
                <!-- <el-button size="mini" type="primary"  @click="setAuthGrant" >机构权限
                </el-button> -->
            </div>
            <div v-show="isOperation" style="padding-right: 10px;">
                <el-button size="mini" type="primary"  @click="updateOrg" >编辑</el-button>
                <el-button size="mini" type="danger"  @click="deleOrg(orgForm)" >删除</el-button>
                <el-button size="mini" type="primary"  @click="addOrg" >新建下级机构</el-button>
                <el-button size="mini" type="primary"  @click="sortOrg" >下级机构排序</el-button>
            </div>
            <div v-show="isChecked" style="padding-right: 10px;">
                <el-button size="mini" type="primary"  @click="saveOrg(orgForm,orgForm2,orgForm3)">保存</el-button>
                <el-button size="mini" type="danger" @click="cancelOrg">取消</el-button>
            </div>
        </div>
        <div class="form">
            <div class="orgType">
                <div class="orgtag">机构标识:</div>
                <el-radio-group v-model="b0194" class="radio-group">
                    <el-radio :label="'1'">法人机构</el-radio>
                    <el-radio :label="'2'">内设机构</el-radio>
                    <el-radio :label="'3'">机构分组</el-radio>
                </el-radio-group>
            </div>
            <div class v-if="b0194 == '1'">
                <el-form class="orgTable" label-width="200px" :model="orgForm" ref="orgForm"
                         :rules="orgRules"  size="small">
                    <el-form-item label="机构全称：" prop="b0101">
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入机构全称"
                                    class="inputmoom"
                                    v-model="orgForm.b0101"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="机构简称：" prop="b0104">
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入机构简称"
                                    class="inputmoom"
                                    v-model="orgForm.b0104"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="机构编码：" prop="b0114">
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入机构编码"
                                    class="inputmoom"
                                    v-model="orgForm.b0114"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="所在政区：" prop="b0117">
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入所在政区"
                                    class="inputmoom"
                                    v-model="orgForm.b0117"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="隶属关系：" prop="b0124">
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入隶属关系"
                                    class="inputmoom"
                                    v-model="orgForm.b0124"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="单位性质类别：" prop="b0131">
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入性质类别"
                                    class="inputmoom"
                                    v-model="orgForm.b0131"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="单位级别：" prop="b0107">
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入级别"
                                    class="inputmoom"
                                    v-model="orgForm.b0127"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="职数描述：" prop="b0134">
                        <el-col :span="16">
                            <el-input
                                    type=" textarea"
                                    autosize
                                    placeholder="职数描述"
                                    class="inputmoom"
                                    v-model="orgForm.b0134"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                </el-form>
            </div>
            <div class v-if="b0194 == '2'">
                <el-form class="orgTable" label-width="200px" :model="orgForm2" ref="orgForm2" size="small">
                    <el-form-item label="机构全称：" prop>
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入机构全称"
                                    class="inputmoom"
                                    v-model="orgForm2.b0101"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="机构简称：" prop="b0104">
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入机构简称"
                                    class="inputmoom"
                                    v-model="orgForm2.b0104"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="机构编码：" prop="b0114">
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入机构编码"
                                    class="inputmoom"
                                    v-model="orgForm2.b0114"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="单位级别：" prop="b0107">
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入级别"
                                    class="inputmoom"
                                    v-model="orgForm2.b0127"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="职数描述：" prop="b0134">
                        <el-col :span="16">
                            <el-input
                                    type=" textarea"
                                    autosize
                                    placeholder="职数描述"
                                    class="inputmoom"
                                    v-model="orgForm2.b0134"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                </el-form>
            </div>
            <div class v-if="b0194 == '3'">
                <el-form class="orgTable" label-width="200px" :model="orgForm3" ref="orgForm3" size="small">
                    <el-form-item label="机构全称：" prop>
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入机构全称"
                                    class="inputmoom"
                                    v-model="orgForm3.b0101"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="机构简称：" prop="b0104">
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入机构简称"
                                    class="inputmoom"
                                    v-model="orgForm3.b0104"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="机构编码：" prop="b0114">
                        <el-col :span="16">
                            <el-input
                                    type="text"
                                    placeholder="输入机构编码"
                                    class="inputmoom"
                                    v-model="orgForm3.b0114"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="职数描述：" prop="b0134">
                        <el-col :span="16">
                            <el-input
                                    type=" textarea"
                                    autosize
                                    placeholder="职数描述"
                                    class="inputmoom"
                                    v-model="orgForm3.b0134"
                                    :disabled="edit"
                            ></el-input>
                        </el-col>
                    </el-form-item>
                </el-form>
            </div>
        </div>

        <!-- 机构合并弹窗 -->
        <org-merge ref="orderAdd" v-if="addOrderVisible" :visible.sync="addOrderVisible"></org-merge>
        <!-- 机构拆分 -->
        <org-demerge ref="orderAdd" v-if="addDemergeVisible" :visible.sync="addDemergeVisible"></org-demerge>
        <!-- 机构权限弹窗 -->
        <!-- <el-dialog
                :title="dialogTitle"
                :visible.sync="dialogGrantVisible"
                :before-close="dialogClose"
                style="padding: 1px 20px;"
                :close-on-click-modal="false"
        >
            <org-auth-grant ref="contentGrant" :orgId="orgCode" @dialogClose="dialogClose"></org-auth-grant>
        </el-dialog> -->
        <!-- 下级机构排序 -->
        <div>
            <el-dialog
                    :title="dialogSortTitle"
                    :visible.sync="dialogSortVisible"
                    :before-close="dialogSortClose"
                    style="padding: 1px 20px;"
                    :close-on-click-modal="false"
            >
                <org-sort ref="contentTable" :orgId="b0121" :labelNames="labelName"
                          @dialogSortClose="dialogSortClose"></org-sort>
            </el-dialog>
        </div>
    </div>
</template>
<script>
   
    import orgMerge from "./orgMerge.vue";
    import orgDemerge from "./orgDemerge.vue";
    // import orgAuthGrant from "./orgAuthGrant.vue";
    import orgSort from "./orgSort.vue";

    export default {
        name: "orgManage",
        //import引入的组件需要注入到对象中才能使用
        components: {
            orgMerge,
            orgDemerge,
            // orgAuthGrant,
            orgSort
        },
        props: {
            orgCode: {
                type: String,
            },
            labelName: {
                type: String,
            },
        },
        data() {
            return {
                clickNode: false,
                dialogSortVisible: false,
                dialogTitle: "权限设置",
                dialogSortTitle: "下级机构排序",
                dialogGrantVisible: false, //添加弹窗
                addDemergeVisible: false,
                edit: true,
                orgRules: {
                    b0101: [
                        {required: true, message: "请输入机构名称", trigger: "blur"}
                    ],
                    b0114: [
                        {required: true, message: '请输入机构编码', trigger: 'blur'}
                    ]
                },
                flag: false,
                addOrderVisible: false,
                isOperation: true,
                isChecked: false,
                radio: 3,
                filterText: "",
                OrgNameQuery: [],
                orgDataTree: [],
                orgForm: {},
                orgForm2: {},
                orgForm3: {},
                b0194: "1", //法人单位标识
                b0121: "",
                orgDicData: {},

            };
        },
        //监听属性 类似于data概念
        computed: {},
        //监控data中的数据变化
        watch: {
            edit(val) {
                if (val) {
                    this.isOperation = true;
                    this.isChecked = false;
                } else {
                    this.isOperation = false;
                    this.isChecked = true;
                }
            }
        },
        //方法集合
        methods: {
            //下级机构排序弹窗
            sortOrg() {
                let _this = this
                if (_this.orgCode) {
                    _this.dialogSortVisible = true;
                    setTimeout(() => {
                        _this.$refs.contentTable.selectOrgSort();
                    }, 10)
                } else {
                    this.$message.warning("请选择需要授权的机构");
                }

            },
            //下级机构排序窗口关闭
            dialogSortClose() {
                let _this = this
                _this.dialogSortVisible = false;
            },
            // //机构权限弹窗
            // setAuthGrant() {
            //     let _this = this;
            //     console.log(_this.orgCode)
            //     if (_this.orgCode) {
            //         _this.dialogGrantVisible = true;
            //         setTimeout(() => {
            //             _this.$refs.contentGrant.initTreeData();
            //         }, 10)
            //     } else {
            //         this.$message.warning("请选择需要授权的机构");
            //     }
            // },
            // //机构权限弹出框关闭
            // dialogClose: function () {
            //     // this.$refs.role.resetRoleForm();
            //     let _this = this;
            //     _this.dialogGrantVisible = false;
            // },
            //机构合并
            toAdd() {
                this.addOrderVisible = true;
            },
            //机构拆分
            toDemerge() {
                this.addDemergeVisible = true;
            },
            //查询政区 隶属关系 单位性质类别 单位级别
            // selectByDictCode(){
            //   let _this = this;
            //   let postData ={dictCode:'ZB01'}
            //   _this.$axios.post(_this.$api.org.selectItemByDictCode,postData).then(response => {
            //     if (response.data.code === 0) {
            //       let res = response.data;
            //       _this.orgDicData = res.data;
            //     } else {
            //       _this.$message.error(response.data.msg);
            //     }
            //   });
            // },

            //通过标识编码查询机构
            selectOrgById() {
                let _this = this;
                if (_this.orgCode != "") {
                    let postData = {b0111: _this.orgCode};
                    _this.$axios
                        .post(_this.$api.org.selectOrgByB0111, postData)
                        .then(response => {
                            if (response.data.code === 0) {
                                let res = response.data;
                                _this.b0121 = res.data.b0111;
                                _this.b0194 = res.data.b0194;
                                _this.orgForm = res.data;
                                _this.orgForm2 = res.data;
                                _this.orgForm3 = res.data;
                            } else {
                                _this.$message.error(response.data.msg);
                            }
                        });
                }
            },
            //编辑
            updateOrg() {
                let _this = this;
                _this.flag = true;
                _this.edit = false;
            },
            //删除
            deleOrg(orgForm) {
                let _this = this;
                _this
                    .$confirm("是否确认此操作?", "提示", {
                        confirmButtonText: "确定",
                        cancelButtonText: "取消",
                        type: "warning"
                    })
                    .then(() => {
                        let postData = {b0111: orgForm.b0111};
                        _this.$axios
                            .post(_this.$api.org.deleteOrg, postData)
                            .then(response => {
                                if (response.data.code === 0) {
                                    _this.$emit("selectOrgTree");
                                } else {
                                    _this.$message.error(response.data.msg);
                                }
                            });
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: "已取消删除"
                        });
                    });
            },
            //新增
            addOrg() {
                let _this = this;
                if (_this.orgCode === null || _this.orgCode === "") {
                    _this.$message.warning("请先选择机构");
                    return false;
                }
                _this.flag = false;
                _this.orgForm = {};
                _this.orgForm2 = {};
                _this.orgForm3 = {};
                _this.edit = false;
            },
            //保存
            saveOrg(orgForm, orgForm2, orgForm3) {
                let _this = this;
                _this.btnEnable = true;
                let postData = {};
                let formName = '';
                if (_this.b0194 == "1") {
                    formName = 'orgForm';
                    postData = orgForm;
                    postData.b0194 = _this.b0194;
                } else if (_this.b0194 == "2") {
                    formName = 'orgForm2';
                    postData = orgForm2;
                    postData.b0194 = _this.b0194;
                } else {
                    formName = 'orgForm3';
                    postData = orgForm3;
                    postData.b0194 = _this.b0194;
                }

                this.$refs[formName].validate((valid) => {
                    if (!valid) return;

                    if (_this.flag === false) {
                        postData.b0121 = _this.b0121;
                        _this.$axios.post(_this.$api.org.insertOrg, postData).then(response => {
                            if (response.data.code === 0) {
                                _this.$message.success(response.data.msg);
                                _this.$emit("selectOrgTree");
                                _this.btnEnable = false;
                                _this.cancelOrg();
                            } else {
                                _this.$message.error(response.data.msg);
                            }
                        });
                    } else {
                        _this.$axios.post(_this.$api.org.updateOrg, postData).then(response => {
                            if (response.data.code === 0) {
                                _this.$message.success(response.data.msg);
                                _this.$emit("selectOrgTree");
                                _this.btnEnable = false;
                                _this.cancelOrg();
                            } else {
                                _this.$message.error(response.data.msg);
                            }
                        });
                    }
                });
            },
            //取消
            cancelOrg() {
                let _this = this;
                _this.edit = true;
            },

        },
        //生命周期 - 创建完成（可以访问当前this实例）
        created() {
        },
        //生命周期 - 挂载完成（可以访问DOM元素）
        mounted() {
            
        },

        beforeCreate() {
        }, //生命周期 - 创建之前

        beforeMount() {
        }, //生命周期 - 挂载之前

        beforeUpdate() {
        }, //生命周期 - 更新之前

        updated() {
        }, //生命周期 - 更新之后

        beforeDestroy() {
        }, //生命周期 - 销毁之前

        destroyed() {
        }, //生命周期 - 销毁完成

        activated() {
        } //如果页面有keep-alive缓存功能，这个函数会触发
    };
</script>

<style lang="scss" scoped>
    .orgButton {
        display: flex;
        justify-content:space-between;
        .left{
            margin-left: 10px;
        }
    }
    .form {
        width: 100%;
        margin-top: 10px;
        .orgType{
            margin: 10px;
            .orgtag {
                font-size: 14px;
                color: #606266;
                display: inline-block;
                width: 182px;
                text-align: right;
                padding: 0 12px 0 0;
            }
            .radio-group {
                padding-left: 8px;
            }   
        }
    }
</style>


